:root {
  --font-body: "Open Sans", sans-serif;
  --font-headings: "Raleway", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --very-dk-blue: hsl(243, 87%, 12%);
  --very-dk-blue-opac: hsl(243, 87%, 12%, .6);
  --desaturated-blue: hsl(238, 22%, 44%);
  --bright-blue: hsl(224, 93%, 58%);
  --moderate-cyan: hsl(170, 45%, 43%);
  --lt-grayish-blue: hsl(240, 75%, 98%);
  --lt-gray: hsl(0, 0%, 75%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 1rem;
  line-height: 1.3;
  font-weight: 400;
  color: var(--very-dk-blue);
  background-color: var(--white);
  text-align: center;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.3;
  font-weight: 700;
  font-family: var(--font-headings);
}
@media (min-width: 40em) {
  h1,
h2,
h3 {
    font-family: var(--font-headings);
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--very-dk-blue);
  font-family: var(--font-headings);
}

.container {
  padding: 0 3rem;
  max-width: 90rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0 5rem;
  }
}
.container-footer {
  padding-top: 50px;
  padding-bottom: 50px;
}

.grid {
  display: grid;
  place-items: center;
  gap: 5rem;
}
@media (min-width: 40em) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.25rem;
  }
  .grid-item1 {
    order: 2;
  }
  .grid-item2 {
    order: 1;
  }
}

.grid-footer {
  display: grid;
  gap: 15px;
  min-height: 21.875rem;
}
@media (min-width: 40em) {
  .grid-footer {
    grid-template-columns: repeat(2, 1fr);
    justify-items: start;
  }
  .grid-footer-item1 {
    order: 1;
  }
  .grid-footer-item2 {
    order: 2;
  }
  .grid-footer-item3 {
    order: 4;
  }
  .grid-footer-item4 {
    order: 3;
  }
}
@media (min-width: 71.875em) {
  .grid-footer {
    grid-template-columns: repeat(4, 1fr);
  }
  .grid-footer > * {
    min-height: 10rem;
  }
  .grid-footer-item1 {
    order: 1;
    align-self: center;
  }
  .grid-footer-item2 {
    order: 2;
    align-self: flex-end;
  }
  .grid-footer-item3 {
    order: 3;
    align-self: flex-end;
  }
  .grid-footer-item4 {
    order: 4;
    align-self: flex-end;
  }
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}
.flex-ac-c {
  align-content: center;
}
.flex-row {
  flex-direction: row;
}
.flex-col {
  flex-direction: column;
}

.nav {
  padding-top: 2.9375rem;
  padding-bottom: 2.9375rem;
  margin-bottom: 1.875rem;
}
.nav__logo {
  display: inline-block;
  font-size: 0;
}
.nav__img {
  width: 8.125rem;
  height: 2.3125rem;
}
@media (min-width: 40em) {
  .nav__img {
    width: 10.4375rem;
    height: 3.0625rem;
  }
}
.nav-links {
  font-size: 0;
  gap: 3.75rem;
}
@media (max-width: 39.9375em) {
  .nav-links {
    display: none;
  }
}
@media (min-width: 71.875em) {
  .nav-links {
    font-size: 0.875rem;
  }
}
.nav-menu * {
  display: block;
}
.nav-menu span {
  width: 30px;
  border: 1px solid #585989;
}
.nav-menu :not(:last-child) {
  margin-bottom: 8px;
}
@media (min-width: 40em) {
  .nav-menu {
    display: none;
  }
}

.fylo {
  margin-bottom: 4.375rem;
}
.fylo__illustration {
  width: 100%;
}
.fylo__header {
  margin-bottom: 2.8125rem;
  font-size: 2.1875rem;
}
@media (min-width: 40em) {
  .fylo__header {
    margin-bottom: 1.75rem;
    font-size: 1.875rem;
    text-align: left;
  }
}
.fylo__content {
  margin-bottom: 2.1875rem;
  line-height: 1.5;
}
@media (min-width: 40em) {
  .fylo__content {
    text-align: left;
    font-size: 1rem;
  }
}
.fylo-form {
  gap: 0.9375rem;
}
@media (min-width: 40em) {
  .fylo-form {
    flex-direction: row;
    width: 90%;
  }
}
.fylo-form__label {
  display: none;
}
.fylo-form__email {
  padding: 0.625rem 1.25rem;
  width: 100%;
}
.fylo-form__submit {
  width: 100%;
  padding: 0.625rem 0;
  color: var(--white);
  background-color: var(--bright-blue);
  font-family: var(--font-headings);
  font-size: 1.125rem;
  font-weight: bold;
  border: none;
  border-radius: 0.3125rem;
  transition: opacity 200ms ease-in-out;
}
.fylo-form__submit:hover {
  cursor: pointer;
  opacity: 0.8;
}

.prod-bg {
  background-image: url("../images/bg-curve-mobile.svg");
  background-position: top;
  background-repeat: no-repeat;
  background-size: 500% 100%;
}
@media (min-width: 40em) {
  .prod-bg {
    background-image: url("../images/bg-curve-desktop.svg");
    background-position: top;
    background-repeat: repeat-y;
    background-size: 450% 100%;
  }
}

.productivity {
  padding-top: 6.25rem;
  padding-bottom: 4.375rem;
  text-align: justify;
}
.productivity__img {
  width: 100%;
}
@media (max-width: 71.8125em) {
  .productivity__header {
    text-align: center;
  }
}
.productivity-link {
  height: 6.25rem;
}
@media (min-width: 71.875em) {
  .productivity-link {
    justify-content: flex-start;
  }
}
.productivity__link {
  border-bottom: 2px solid;
  font-size: 0.875rem;
  color: var(--moderate-cyan);
}
.productivity-card {
  max-width: 21.875rem;
  margin: 0 auto;
  padding: 1.875rem;
  font-size: 0.875rem;
  box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.15);
}
.productivity-card p {
  margin-top: 0;
  margin-bottom: 1.25rem;
  line-height: 1.5;
}
.productivity-card-username {
  gap: 0.625rem;
  font-size: 0.625rem;
}
.productivity-card-username img {
  width: 3.125rem;
  border-radius: 50%;
}
.productivity-card-username span {
  font-weight: bold;
  font-size: 0.875rem;
}
@media (min-width: 71.875em) {
  .productivity-card {
    margin: 0;
  }
}

footer {
  color: var(--white);
}

.footer1-bg {
  background-color: var(--very-dk-blue-opac);
}

.footer1 {
  font-family: var(--font-headings);
}
.footer1 > * {
  flex: 1;
}
@media (min-width: 40em) {
  .footer1 {
    text-align: left;
    flex-direction: row;
    gap: 12.5rem;
  }
}
@media (max-width: 71.8125em) {
  .footer1__signup {
    margin-bottom: 0.625rem;
  }
}
.footer1__label {
  display: none;
}
.footer1__email {
  width: 90%;
  border: none;
  border-radius: 0.125rem;
  padding: 0.625rem;
  margin-bottom: 0.625rem;
}
@media (min-width: 71.875em) {
  .footer1__email {
    width: 100%;
    padding: 0.9375rem;
  }
}
.footer1__submit {
  width: 90%;
  background-color: var(--bright-blue);
  padding: 0.625rem;
  border: none;
  border-radius: 0.125rem;
  cursor: pointer;
  color: var(--white);
  font-weight: bold;
  transition: background-color 200ms ease-in-out;
}
.footer1__submit:hover {
  background-color: #537ff9;
}
@media (min-width: 71.875em) {
  .footer1__submit {
    width: 40%;
    padding: 0.9375rem;
  }
}

.footer2-bg {
  background-color: var(--very-dk-blue);
}

.footer2 {
  text-align: left;
}
.footer2 a {
  color: var(--white);
}
.footer2 ul {
  margin: 0;
}
.footer2-list {
  list-style-type: none;
  list-style-position: outside;
  padding-left: 0;
  text-align: left;
  line-height: 2;
}
.footer2__phone::before {
  content: url("../images/icon-email.svg");
  margin-right: 0.625rem;
}
.footer2__email::before {
  content: url("../images/icon-phone.svg");
  margin-right: 0.625rem;
}
.footer2-icon {
  gap: 1.25rem;
  list-style-type: none;
  list-style-position: outside;
  padding-left: 0;
}
.footer2-icon li {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1.875rem;
  height: 1.875rem;
  border: 0.0625rem solid;
  border-radius: 50%;
  overflow: hidden;
}
.footer2-icon li a {
  display: inline-block;
  padding: 0.625rem;
}